import React, { useEffect, useState } from "react";
import {
  Typography,
  Card,
  Stack,
  Paper,
} from "@mui/material";
import problemsData from "../../json/userdata.json";
import CardContents from "./CardContents";
import { styled } from "@mui/material/styles";

export default function CardsContent({ setSelectedTitle, currentPage, handlePageChange }) {
  const [rowsPerPage] = useState(5); // Number of cards per page
  const [problemDetails, setProblemDetails] = useState(null);
  const [selectedDifficulty, setSelectedDifficulty] = useState("All");

  const startIndex = (currentPage - 1) * rowsPerPage;
  const endIndex = startIndex + rowsPerPage;
  const displayedProblems = problemDetails ? problemDetails.slice(startIndex, endIndex) : [];

  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#1d0925",
    ...theme.typography.body2,
    padding: theme.spacing(1),
    textAlign: "center",
    color: theme.palette.text.secondary,
    flexGrow: 1,
  }));

  useEffect(() => {
    // Assuming problemsData is an array of objects containing problem details
    setProblemDetails(problemsData);
  }, []);

  useEffect(() => {
    setSelectedDifficulty("All"); // Initialize with "All" after problemDetails is set
  }, [problemDetails]);

  const filterProblems = (selectedDifficulty) => {
    if (!problemDetails) return [];
    
    if (selectedDifficulty === "All") {
      return problemDetails; // Return all problems
    } else {
      return problemDetails.filter((problem) => problem.difficulty === selectedDifficulty);
    }
  };

  const filteredProblems = filterProblems(selectedDifficulty);

  const handleDifficultyChange = (event) => {
    setSelectedDifficulty(event.target.value);
  };

  return (
    <>
      <div className="filter-dropdown">
        <label htmlFor="difficulty">Filter by Difficulty:</label>
        <select
          id="difficulty"
          name="difficulty"
          value={selectedDifficulty}
          onChange={handleDifficultyChange}
        >
          <option value="All">All</option>
          <option value="Easy">Easy</option>
          <option value="Medium">Medium</option>
          <option value="Hard">Hard</option>
        </select>
      </div>

      {problemDetails && (
        <>
          {filteredProblems.length > 0 ? (
            filteredProblems.map((problem, index) => (
              <Card
                style={{ border: "4px", borderBlockColor: "pri" }}
                key={index}
                sx={{ marginTop: "10px" }}
                xs={2}
                sm={2}
                md={2}
                lg={2}
              >
                <Stack
                  spacing={{ xs: 1, sm: 2 }}
                  direction="row"
                  useFlexGap
                  flexWrap="wrap"
                >
                  <Item>
                    {" "}
                    <CardContents style={{backgroundColor:""}} setSelectedTitle={setSelectedTitle} title={problem.title}>
                    <Typography className="white-text">{problem.details}</Typography>

                    </CardContents>{" "}
                  </Item>
                </Stack>
              </Card>
            ))
          ) : (
            <Typography>Loading...</Typography>
          )}
        </>
      )}

      {/* Pagination controls */}
      <div className="pagination">
        <button
          onClick={() => handlePageChange(currentPage - 1)}
          disabled={currentPage === 1}
        >
          Prev
        </button>
        <span>Page {currentPage}</span>
        <button
          onClick={() => handlePageChange(currentPage + 1)}
          disabled={displayedProblems.length < rowsPerPage}
        >
          Next
        </button>
      </div>
    </>
  );
}
