import React, { useState } from 'react';

function BinarySearchApp() {
  const [arrayInput, setArrayInput] = useState('');
  const [targetInput, setTargetInput] = useState('');
  const [result, setResult] = useState(-1);

  const handleBinarySearch = () => {
    // Parse the user input for the array and target
    const inputArray = arrayInput.split(',').map((str) => parseInt(str.trim()));
    const target = parseInt(targetInput);

    if (!isNaN(target) && Array.isArray(inputArray)) {
      let start = 0;
      let end = inputArray.length - 1;

      while (start <= end) {
        const mid = start + Math.floor((end - start) / 2);

        if (target < inputArray[mid]) {
          end = mid - 1;
        } else if (target > inputArray[mid]) {
          start = mid + 1;
        } else {
          // Target found
          setResult(mid);
          return;
        }
      }

      // Target not found
      setResult(-1);
    }
  };

  return (
    <div>
      <h1>Binary Search App</h1>
      <div>
        <label>
          Enter the array (comma-separated):{' '}
          <input
            type="text"
            value={arrayInput}
            onChange={(e) => setArrayInput(e.target.value)}
          />
        </label>
      </div>
      <div>
        <label>
          Enter the target value:{' '}
          <input
            type="text"
            value={targetInput}
            onChange={(e) => setTargetInput(e.target.value)}
          />
        </label>
      </div>
      <button onClick={handleBinarySearch}>Search</button>
      <p>Result: {result === -1 ? 'Not found' : `Found at index ${result}`}</p>
    </div>
  );
}

export default BinarySearchApp;
